#experience {
  color: #768390; }
  #experience .timeline {
    line-height: 1.4em;
    list-style: none;
    margin: 0;
    padding: 0;
    width: 100%; }
    #experience .timeline h3 {
      color: #000;
      line-height: inherit; }
    @media only screen and (min-width: 768px) {
      #experience .timeline {
        display: table; }
        #experience .timeline,
        #experience .timeline .timeline-item,
        #experience .timeline .timeline-info,
        #experience .timeline .timeline-marker,
        #experience .timeline .timeline-content {
          display: block;
          margin: 0;
          padding: 0; }
        #experience .timeline > .timeline-item:nth-child(odd) .timeline-info {
          float: left;
          text-align: right;
          padding-right: 2rem; }
        #experience .timeline > .timeline-item:nth-child(odd) .timeline-content {
          float: right;
          text-align: left;
          padding-left: 2rem; }
        #experience .timeline > .timeline-item:nth-child(even) .timeline-info {
          float: right;
          text-align: left;
          padding-left: 2rem; }
        #experience .timeline > .timeline-item:nth-child(even) .timeline-content {
          float: left;
          text-align: right;
          padding-right: 2rem; } }
  #experience .timeline-item {
    position: relative;
    padding-left: 2.5rem; }
    @media only screen and (min-width: 768px) {
      #experience .timeline-item {
        padding-bottom: 5rem !important;
        overflow: hidden;
        height: 15rem; } }
    #experience .timeline-item:first-child {
      margin-top: 3em; }
    #experience .timeline-item:last-child {
      padding-bottom: 0; }
      #experience .timeline-item:last-child:after {
        content: none; }
    #experience .timeline-item .timeline-info {
      font-weight: bold;
      letter-spacing: .1rem;
      margin: 0 0 .5em 0;
      text-transform: uppercase;
      white-space: nowrap; }
      @media only screen and (min-width: 768px) {
        #experience .timeline-item .timeline-info {
          width: 50%; } }
    #experience .timeline-item .timeline-marker {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      width: 15px; }
      @media only screen and (min-width: 768px) {
        #experience .timeline-item .timeline-marker {
          left: 50%;
          margin-left: -7.5px; } }
      #experience .timeline-item .timeline-marker:before {
        content: "";
        position: absolute;
        top: 4px;
        left: 0;
        display: block;
        width: 15px;
        height: 15px;
        border: 3px solid transparent;
        border-radius: 100%;
        background: #FF6B6B;
        -webkit-transition: background .3s ease-in-out, border .3s ease-in-out;
        -o-transition: background .3s ease-in-out, border .3s ease-in-out;
        transition: background .3s ease-in-out, border .3s ease-in-out; }
      #experience .timeline-item .timeline-marker:after {
        content: "";
        position: absolute;
        top: 24px;
        left: 6px;
        bottom: 0;
        display: block;
        width: 3px;
        background: #CCD5DB; }
    #experience .timeline-item .timeline-content {
      padding-bottom: 3em; }
      @media only screen and (min-width: 768px) {
        #experience .timeline-item .timeline-content {
          width: 50%; } }
  #experience .timeline-item:hover .timeline-marker:before {
    border: 3px solid #FF6B6B;
    background: transparent; }



